#footer{
    background: var(--glow-1);
}
#footer .row{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    justify-content: space-between;
    gap: 2rem;
}

#footer .row .column{
    width: 100%;
}

#footer .routes__name{
    font-size: 18px;
    margin-bottom: 15px;
    font-weight: 700;
}

#footer .routes__container .route__item{
    display: block;
    transition: var(--transition);
    font-size: 16px;
    margin: 10px 0;
    cursor: pointer;
    font-weight: 600;
}

#footer .routes__container .route__item:hover{
    color: var(--color-primary);
}

#footer .newsletter{
    margin-top: 10px;
}

#footer .newsletter h3{
    margin-bottom: 20px;
}

#footer .newsletter .control__container{
    display: flex;
    align-items: stretch;
    max-width: 500px;
    margin: auto;
    border-radius: var(--radius-2);
    overflow: hidden;
}

#footer .newsletter .control{
    flex: 1;
    display: block;
    padding: 15px;
    background: var(--color-ui-secondary);
    color: var(--color-text);
    font-size: 15px;
    overflow: hidden;
    transition: var(--transition);
}

#footer .newsletter .btn{
    border-radius: 0;
    background: var(--gradient-2);
    color: var(--color-white);
}

#footer .copyright{
    background: var(--glow-2);
}

#footer .copyright .container{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 10px;
    text-align: center;
    padding: 40px 20px ;
}

#footer .copyright h3{
    font-size: 16px;
    font-weight: 500;
    color: var(--color-text);
}

@media (max-width:900px) {
    #footer .row{
        grid-template-columns: repeat(2,1fr);
    }
}

@media (max-width:768px) {
    #footer .row{
        grid-template-columns: 100%;
    }
    #footer .logo{
        justify-content: center;
    }
    #footer .newsletter h3{
        text-align: center;
    }
}