
#services .services__container{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap: 20px;
    margin-top: 50px;
}

@media (max-width:1040px) {
    #services .services__container{
        grid-template-columns: repeat(2,1fr);
    }
}

@media (max-width:700px) {
    #services .services__container{
        grid-template-columns: 100%;
    }
}
