#contact{
    overflow: hidden;
}

#contact .container{
    display: flex;
    gap: 30px;
}

#contact .contact__form{
    background: linear-gradient(
        to bottom right, 
        var(--color-ui-primary), 
        var(--color-ui-secondary)
        );
    padding: 40px;
    border-radius: var(--radius-3);
    width: 100%;
    max-width: 500px;
    box-shadow: var(--shadow-1);
}

#contact .title{
    font-size: 40px;
    text-align: left;
}

#contact .row{
    display: flex;
    gap: 20px;
    justify-content: space-between;
}

#contact .control{
    display: block;
    width: 100%;
    padding: 10px;
    margin: 20px 0;
    background: var(--color-text);
    border-radius: var(--radius-2);
    color: var(--color-text);
    font-size: 15px;
    overflow: hidden;
    transition: var(--transition);
    border: var(--gray-border);
    border-width: 2px;
    resize: none;
}

input{
    height: 50px;
}

#contact .control:focus{
    border-color: var(--color-primary);
}

#contact .contact__form__bottom{
    display: flex;
    align-items: center;
    margin-top: 30px;
}

#contact .contact__options{
    flex: 1;
    display: flex;
    flex-direction: column;
    gap: 1.2rem;
}

#contact .contact__options .option{
    border-radius: var(--radius-3);
    background: linear-gradient(
        to bottom right, 
        var(--color-ui-primary), 
        var(--color-ui-secondary)
        );
    box-shadow: var(--shadow-1);
    transition: var(--transition);
    padding: 1.2rem;
    text-align: center;
}

#contact .contact__options .option:hover{
    transform: scale(0.98);
    border-color: var(--color-primary);
    background: linear-gradient(
        to bottom right, 
        var(--color-ui-primary), 
        var(--color-ui-secondary)
        );
}

#contact .contact__options .icon__container{
    width: 4rem;
    height: 4rem;
    font-size: 25px;
    background: var(--color-ui-primary);
    border-radius: var(--radius-5);
}

#contact .contact__options .name{
    font-size: 16px;
    font-weight: 600;
    margin: 10px 0;
}

#contact .contact__options .text__muted{
    font-size: 15px;
}

#contact .contact__options .btn{
    margin-top: 20px;
}

@media (max-width:800px) {
    #contact .container{
        flex-direction: column;
        align-items: stretch;
    }
    
    #contact .contact__form{
        max-width: 100%;
        padding: 20px;
    }
}